/* HOME GRID */
.cards-grid {
  position: relative;
  z-index: 5;
  margin-top: 60px;
  margin-right: -15px;
  &--portfolio {
    margin-top: -160px; } }

.grid-sizer {
  width: calc(25% - 15px); }

/* card and all mods */
.card {
  width: calc(25% - 15px);
  margin-bottom: 15px;
  position: relative;
  overflow: hidden;
  display: inline-block;
  box-shadow: 0 30px 30px 0 #D0DEE2;
  border-radius: 20px;
  transition: box-shadow .25s $ease, margin .25s $ease, background-position .25s $ease;
  &:hover {
    box-shadow: 0 30px 30px -15px #D0DEE2;
    // transform: translate3d(0,-7px,0)
    margin-top: -7px;
    .card__content-icon {
      .ico-arrow {
        transform: scale(1.5); } } }
  &__bg {
    font-size: 0;
    svg, img {
      width: 100%;
      max-width: 100%; } }

  &__content {
    display: flex;
    align-items: flex-end;
    // margin-top: 10px
    padding: 20px 20px 10px 20px;
    color: white;
    &-wrap {
      flex: 1 1 auto;
      min-width: 0;
      min-height: 1px; }
    &-icon {
      flex: 0 0 auto;
      padding-left: 10px;
      padding-bottom: 5px;

      .ico {
        font-size: 13px;
        &-arrow {
          transform-origin: right center;
          transition: .25s $ease; } } }
    &--fixed {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 2; }
    &--black {
      background: $fontColor; } }


  &__title {
    font-family: $monster;
    font-weight: 900;
    font-size: 25px;
    @include text-overflow;
    span {
      font-size: 16px; } }

  &__desc {
    font-size: 14px;
    line-height: 2;
    @include text-overflow; }

  &--purple-bg {
    background-image: linear-gradient(134deg, #3023AE 0%, #C86DD7 100%); }

  &--white-bg {
    background-image: linear-gradient(134deg, #F5FBFD 0%, #FFFFFF 100%); }
  &--dark-bg {
    background-image: linear-gradient(127deg, #292A2C 0%, #28282A 100%); }

  &--contact {
    background-image: linear-gradient(134deg, #F5FBFD 0%, #FFFFFF 100%);
    .card {
      &__title {
        font-size: 20px; }
      &__content {
        align-items: center;
        color: $fontColor;
        padding: 40px 20px 25px 20px;
        &-icon {
          padding-bottom: 0; } } } }
  &--case {
    box-shadow: none; }

  &--mailer, &--mailer-black {
    width: 310px;
    margin-top: 30px;
    background-size: 230% 100%;
    .card__content {
      color: white; }
    &:hover {
      margin-top: 30px;
      background-position: 100% 100%;
      .card__content-icon {
        filter: brightness(0) invert(100%); }
 } }      // background-image: linear-gradient(25deg, #3CAAC4 0%, #7CD2E6 56%, #B4EC51 100%)
  &--mailer {
    background-image: linear-gradient(134deg, #3023AE 0%, #C86DD7 50%, #3CAAC4 50%, #7CD2E6 78%, #B4EC51 100%);
    &-black {
      background-image: linear-gradient(134deg, #292A2C 0%, #292A2C 50%, #3CAAC4 50%, #7CD2E6 78%, #B4EC51 100%); } }

  &--mailer-white {
    width: 310px;
    margin-top: 20px;
    background-size: 230% 100%;
    background-image: linear-gradient(134deg, #F5FBFD 0%, #FFFFFF 50%, #3CAAC4 50%, #7CD2E6 78%, #B4EC51 100%);
    .card__title, .card__desc {
      transition: .25s .06s $ease; }
    &:hover {
      margin-top: 20px;
      background-position: 100% 100%;
      .card__content-icon {
        filter: brightness(0) invert(100%); }
      .card__title, .card__desc {
        color: white; } } } }

@include r(1200) {
  .grid-sizer {
    width: calc(33% - 15px); }

  .card {
    width: calc(33% - 15px);
    &--mailer, &--mailer-black, &--mailer-white {
      width: 310px; } } }

@include r(900) {
  .grid-sizer {
    width: calc(50% - 15px); }

  .card {
    width: calc(50% - 15px);
    &--mailer, &--mailer-black, &--mailer-white {
      width: 310px;
      margin-top: 20px;
      &:hover {
        margin-top: 20px; }
      .card {
        &__content {
          padding: 30px 20px 20px 20px; } } } } }

@include r($tablet) {
  .cards-grid {
    &--portfolio {
      margin-top: -80px; } } }

@include r($mobile) {
  .grid-sizer {
    width: calc(100% - 15px); }

  .card {
    width: calc(100% - 15px);
    margin-bottom: 20px;
    &:hover {
      margin-top: 0;
      margin-left: -7px; }
    &--mailer, &--mailer-black, &--mailer-white {
      width: 310px;
      &:hover {
        margin-top: 20px; } } } }


@include r(414) {
  .card {
    &--mailer, &--mailer-black, &--mailer-white {
      width: 280px;
      .card {
        &__title {
          font-size: 18px; }
        &__desc {
          font-size: 13px; } } } } }

/* Similar services - more cards */
.more-cards {
  margin-top: 100px; }
